*, :after, :before {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  line-height: 1.6;
}

.container {
  max-width: 1158px;
  margin: 0 auto;
}

.page-footer {
  background-color: #f7f7f7;
  flex-direction: column;
  justify-content: center;
  height: 100px;
  display: flex;
}

.footer-text {
  color: #545454;
  text-align: center;
  letter-spacing: .03em;
  max-width: 264px;
  font-size: 14px;
  line-height: 1.6;
}

.footer {
  flex-direction: column;
  display: flex;
}

.container-footer {
  margin: auto;
}

.heart {
  fill: #ff6b08;
  width: 14px;
  margin: 0 8px -2px;
  position: relative;
}

.team-list {
  text-align: center;
  flex-flow: column;
  align-items: center;
  gap: 24px;
  margin-top: 30px;
  padding-top: 50px;
  list-style: none;
  display: flex;
}

.team {
  background: url("../background.56376ee6.jpeg") 50% / cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 120px 0;
  display: flex;
}

.team-text {
  text-align: center;
  letter-spacing: .02em;
  color: #1f1919;
  text-align: center;
  font-size: 32px;
  line-height: 1.11;
  animation: 1.5s linear infinite animate;
}

@keyframes animate {
  0% {
    opacity: 0;
  }

  50% {
    opacity: .9;
  }

  100% {
    opacity: 0;
  }
}

.team-member {
  text-align: center;
  background-color: #f4f4fd;
  border-radius: 0 0 4px 4px;
  height: 450px;
  box-shadow: 0 2px 1px #2e2f4214, 0 1px 1px #2e2f4229, 0 1px 6px #2e2f4214;
}

.member-names {
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header-text {
  letter-spacing: .02em;
  color: #434455;
  font-size: 14px;
  line-height: 1.5;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.img {
  object-fit: cover;
  border-radius: 5px;
  max-width: 100%;
  height: 350px;
  display: block;
}

@media screen and (width >= 320px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (width >= 768px) {
  .container {
    max-width: 768px;
  }

  .container-footer {
    margin: 0;
  }

  .footer-text {
    max-width: 100%;
  }

  .page-footer {
    height: 79px;
  }

  .footer {
    flex-direction: row;
    gap: 72px;
  }

  .team-list {
    flex-flow: wrap;
    justify-content: center;
    display: flex;
  }

  .team-member {
    flex-wrap: wrap;
    flex-basis: calc(50% - 24px);
    max-width: 264px;
    display: flex;
  }
}

@media screen and (width >= 1280px) {
  .container {
    max-width: 1280px;
  }

  .footer {
    flex-wrap: nowrap;
  }

  .team-list {
    flex-flow: wrap;
    justify-content: center;
    display: flex;
  }

  .team-member {
    flex-basis: calc(25% - 24px);
  }
}
